.home__video__contents {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 0;
  padding-bottom: 56.5%;
}

.home__video__contents img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}

.home__video__contents iframe {
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  position: absolute;
  background: #000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  opacity: 0;
  pointer-events: none;
  border: none;
}

.home__video.-showvideoinplace iframe {
  opacity: 1;
  pointer-events: all;
}

.home__modal {
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  opacity: 0;
  pointer-events: none;
}

.home__modal__backdrop {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.75);
}

.home__modal__container {
  width: 90%;
  max-width: 900px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.home__modal .home__video {
  width: 100%;
}

.home__modal.-open {
  opacity: 1;
  pointer-events: all;
}

.home__modal.-open .home__video__contents iframe {
  opacity: 1;
  pointer-events: all;
}

